function loadingScreenRemove(){
    document.querySelector('#loadingScreen').classList.remove('fadeIn');
    document.querySelector('#loadingScreen').classList.add('fadeOut');
    setTimeout(() => {
      document.querySelector('#loadingScreen').remove();
    }, 500);
}
